.nav-header {
  display: flex;
  justify-content: center;
  background: linear-gradient(120deg, #2eba7c, #2ebab7);
}

.nav-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  max-width: 1110px;
  padding-top: 25px;
  padding-bottom: 25px;
}

.website-logo {
  width: 110px;
}

.nav-menu {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-self: flex-end;
  flex: 1;
  list-style-type: none;
  margin-top: 0px;
  margin-bottom: 0;
}

.nav-link {
  font-family: "Roboto";
  text-decoration: none;
  margin: 10px;
  font-weight: bold;
  font-size: 16px;
  color: #475569;
}

.logout-desktop-btn {
  font-family: "Roboto";
  font-weight: 600;
  font-size: 10px;
  padding: 8px 16px;
  color: white;
  background-color: #0967d2;
  border: none;
  border-radius: 4px;
  margin-left: 14px;
}

.logout-mobile-btn {
  border: none;
  padding: 0px;
  background: transparent;
}

.logout-icon {
  width: 24px;
}
.size {
  font-size: 12px;
}
